import React from 'react';
import Grid from 'material-ui/Grid';

import GridItem from '../components/grid-item';
import PostFull from '../components/post-full';

class BlogPostTemplate extends React.Component {
  render() {
    const post = {};
    post.html = this.props.data.markdownRemark.html;
    post.title = this.props.data.markdownRemark.frontmatter.title;
    post.date = this.props.data.markdownRemark.frontmatter.date;
    post.tags = this.props.data.markdownRemark.frontmatter.tags;
    
    return (
      <div>
        {/* <Hero backgroundImageWide={lrgImage} backgroundImageNarrow={smlImage} /> */}
        <Grid container spacing={0}>
          <GridItem page={post} PostType={PostFull} />
        </Grid>
      </div>

    )
  }
}

export default BlogPostTemplate

export const pageQuery = graphql`
query BlogPostBySlug($slug: String!) {
    markdownRemark(
      fields: {
        slug: {
          eq: $slug
        }
      }
    )
    {
    html
    frontmatter {
      title
      date
      path
      parent
      tags
    }
  }
}
`;



