import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from 'material-ui/styles';
import Typography from 'material-ui/Typography';

import TagArray from '../tag-array';

const propTypes = {

};

const styles = theme => ({
  root: {
    padding: 16,
    textAlign: 'left',
    color: theme.palette.text.secondary,
    marginTop: 20,
  },
});

const PostFull = (props) => {
  const { classes, html, path, title, tags } = props;
  return (
    <div className={classes.root}>
      <Typography type="title" gutterBottom>{title}</Typography>
      <div dangerouslySetInnerHTML={{ __html: html }} />
      <TagArray tags={tags} navClick />
    </div>
  );
};

PostFull.propTypes = propTypes;

export default withStyles(styles, { name: 'StyledPostFull' })(PostFull);



